.App {
  background-color: transparent;
  width: 100%;
  height: auto;
  padding-top: 1.5rem;
  padding-left: 5rem;
  padding-right: 5rem;
  padding-bottom: 2rem;
}
@media screen and (min-width: 280px) and (max-width: 694px) {
  .App {
    background-color: transparent;
    width: 100%;
    height: auto;
    padding-top: 1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-bottom: 1rem;
  }
}